header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.headerContainer {
    text-align: center;
    height: 100%;
    position: relative;
}


.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.headerSocials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.headerSocials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 10px 100px / 120px;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
.scrollDown{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}



/* =================== MEDIA QUERIES (MEDIUM DEVICES) =============== *\
 */

@media screen  and (max-width: 1024px) {
header{
    height: 68vh;
}
}

/* =================== MEDIA QUERIES (SMALL DEVICES) =============== *\
 */

@media screen  and (max-width: 600px) {
header{
    height: 100vh;
}
   .headerSocials, .scrollDown{
        display: none;
    }
}